@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
body {
  min-width: 800px;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
